import React from 'react'
import Layout from '../components/Layout'
import { graphql, useStaticQuery } from 'gatsby'
import { getImage } from "gatsby-plugin-image"
import { BgImage } from "gbimage-bridge"

export default function Contact() {
    const { placeholderImage } = useStaticQuery(
        graphql`
          query {
            placeholderImage: file(relativePath: {eq: "Slak.jpg"}) {
              childImageSharp {
                gatsbyImageData(
                  width: 1600
                  quality: 80
                  webpOptions: {quality: 90}
                  formats: [AUTO, WEBP, AVIF]
                )
              }
            }
          }
          
        `
      )
      const pluginImage = getImage(placeholderImage);
    
    return (
        <Layout>
             <BgImage image={pluginImage} className="bg"
      Tag="section">
          <div className="contactHeading">
          <h1 className="contactHeading">CONTACT</h1>
          </div>
          </BgImage>
        <div className="genText">
  <p>Contactgegevens: <br/>
  <a href="mailto:info@aikyam.nl">info@aikyam.nl</a><br/>
+31 6 1109 3287</p>
<p>Of vul het formulier in:</p>
<p></p>
        <div class="container form-top">
                <div class="row">
                    <div class="col-md-6 col-md-offset-3 col-sm-12 col-xs-12">
                        <div class="panel panel-danger">
                            <div class="panel-body">
                            <form id="reused_form" method="post" action="https://formspree.io/f/xjvjryrq">
                                    <div class="form-group">
                                        <label><i class="fa fa-user" aria-hidden="true"></i> Naam</label>
                                        <input type="text" name="naam" class="form-control" placeholder="Enter Naam"/>
                                    </div>
                                     <div class="form-group">
                                        <label><i class="fa fa-envelope" aria-hidden="true"></i> Email</label>
                                        <input type="email" name="_replyto" class="form-control" placeholder="Enter Email"/>
                                    </div>
                                    <div class="form-group">
                                        <label><i class="fa fa-comment" aria-hidden="true"></i> Bericht</label>
                                        <textarea rows="3" name="bericht" class="form-control" placeholder="Typ Je Bericht"></textarea>
                                    </div>
                                    <div class="form-group">
                                        <button type="submit" class="btn btn-raised btn-block btn-warning">Verstuur &rarr;</button>
                                    </div>
                                </form>
                                  </div>
                            </div>
                    </div>
                </div>
            </div>

            <p className="mt-5"><a href="https://www.divineloveshower.com/">
            Nepal: Divine Love Shower</a></p>
<p><a href="https://ava-ev.de/">
Duitsland: AVA</a></p>
<p> <a href="https://www.weareoneong.com/">Spanje: We Are One  </a>
</p>
<p><a href="https://www.divinebreathfoundation.org/">
AustraliÃ«: Divine Breath Foundation </a>
            </p>
        </div>
        </Layout>
    )
}
